.character-card {
  width: 200px;
  border: 2px solid #333;
  border-radius: 10px;
  padding: 10px;
  background-color: white;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
}

.character-card.player {
  border-color: #4a90e2;
}

.character-card.enemy {
  border-color: #e24a4a;
}

.character-image {
  width: 100%;
  height: 150px;
  object-fit: cover;
  border-radius: 5px;
}

.character-info h2 {
  margin: 10px 0;
}