/* Hand Component */
.hand {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

/* Card Component */
.card {
  width: 150px;
  border: 2px solid #ddd;
  border-radius: 8px;
  margin: 10px;
  padding: 10px;
  background-color: #f9f9f9;
  transition: all 0.3s ease;
  cursor: pointer;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
}

.card.selected {
  border-color: #4a90e2;
  box-shadow: 0 0 10px #4a90e2;
}

.card-image {
  width: 100%;
  height: 100px;
  object-fit: cover;
  border-radius: 4px;
}

.card-content {
  margin-top: 10px;
}

.card-type {
  font-style: italic;
  color: #666;
}

.card-description {
  font-size: 0.9em;
  margin: 5px 0;
}

.card-energy {
  font-weight: bold;
  color: #4a90e2;
}

/* Turn Visualizer Component */
.turn-visualizer {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 0 20px rgba(0,0,0,0.2);
  z-index: 1000;
  max-width: 80%;
  max-height: 80%;
  overflow-y: auto;
}

.turn-visualizer h2, .turn-visualizer h3 {
  color: #4a90e2;
  margin-bottom: 15px;
}

.turn-visualizer p {
  margin: 5px 0;
}

/* Character Card Component */
.character-card {
  width: 200px;
  border: 2px solid #ddd;
  border-radius: 10px;
  padding: 15px;
  background-color: #f9f9f9;
  margin: 10px;
}

.character-card.player {
  border-color: #4a90e2;
}

.character-card.enemy {
  border-color: #e24a4a;
}

.character-image {
  width: 100%;
  height: 150px;
  object-fit: cover;
  border-radius: 5px;
}

.character-info h2 {
  margin: 10px 0;
  color: #333;
}

.character-info p {
  margin: 5px 0;
  color: #666;
}

.enemy-card {
  margin-top: 20px;
  text-align: center;
}

.enemy-card .card {
  display: inline-block;
  width: 200px;
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 10px;
  margin: 10px;
  background-color: #f9f9f9;
}

.enemy-card .card-image {
  width: 100%;
  height: auto;
  border-radius: 5px;
}

.enemy-card .card-content {
  margin-top: 10px;
}